.footer {
  background-color: rgb(0, 0, 67);
  padding: 30px;

  * {
    color: #ffffff;
    font-size: 1.3rem;
  }

  .content {
    display: flex;

    & > * {
      flex-grow: 1;
    }

    .left {
      text-align: center;

      .signature {
        padding: 20px 40px;
        user-select: none;
        cursor: pointer;
        
    
        a {
          font-size: 3rem;
          font-weight: bold;
          color: #ffa41b;
        }
      }

      span {
        font-size: 1.2rem;
      }
    }

    .middle, .right {
      .title {
        font-weight: bold;
        margin: 20px 0;
      }

      ul  {

        * {
          color: #e1e1e1;
        }

        li {
          margin: 10px 0;

          a {
            cursor: pointer;
          }

          svg {
            margin-right: 10px;
          }
        }
      }
    }
  }

  .greeting {
    text-align: center;
    margin-top: 40px;

    &-content {
      span {
        font-size: 1rem;
        color: #e3e3e3;
      }
    }

    .contact-link {
      margin-top: 40px;

      ul {
        display: flex;
        justify-content: center;
        gap: 20px;

        li a {
          width: 20px;
          height: 20px;
          border: 1px solid rgba($color: #000000, $alpha: 0.2);
          display: flex;
          align-items: center;
          justify-content: center;
          border-radius: 5px;
          padding: 6px;
          transition: 1s;

          svg {
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }
}

